import React from 'react';
import { EditIcon, TrashBinIcon, EnvelopeIcon } from '@/icons';
import Button from '../ui/Button';

type Message = {
  id: number;
  mensagem: string;
  tipo: 'A_VENCER' | 'NO_DIA' | 'VENCIDA' | 'COMPROVANTE' | 'MANUAL' | 'FINALIZACAO';
  status: boolean;
  hora: string;
  dias: number;
};

type MessageCardProps = {
  message: Message;
  onEdit: () => void;
  onDelete: () => void;
};

const messageTypeColors = {
  A_VENCER: 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-300',
  NO_DIA: 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-300',
  VENCIDA: 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-300',
  COMPROVANTE: 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-300',
  MANUAL: 'bg-purple-100 text-purple-800 dark:bg-purple-900/20 dark:text-purple-300',
  FINALIZACAO: 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-300'
};

const messageTypeNames = {
  A_VENCER: 'A Vencer',
  NO_DIA: 'No Dia',
  VENCIDA: 'Vencida',
  COMPROVANTE: 'Comprovante',
  MANUAL: 'Manual',
  FINALIZACAO: 'Finalização'
};

const MessageCard: React.FC<MessageCardProps> = ({ message, onEdit, onDelete }) => {
  // Função para formatar a mensagem destacando as variáveis
  const formatMessage = (text: string) => {
    text = text.slice(0, 150)
    return text.split(/(\[[^\]]+\])/).map((part, index) => {
      if (part.match(/^\[[^\]]+\]$/)) {
        return (
          <span key={index} className="px-1 bg-brand-100 text-brand-600 rounded dark:bg-brand-900/20 dark:text-brand-400">
            {part}
          </span>
        );
      }
      return part;
    });
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md transition-shadow shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-5">
      <div className="flex items-start justify-between mb-4">
        <div className="flex items-center space-x-3">
          <div className="bg-gray-100 dark:bg-gray-700 rounded-full p-2">
            <EnvelopeIcon className="text-gray-500 dark:text-gray-400" />
          </div>
          <div>
            <span className={`px-2.5 py-0.5 text-xs font-medium rounded-full ${messageTypeColors[message.tipo]}`}>
              {messageTypeNames[message.tipo]}
            </span>
            <div className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              {message.status ? 'Ativo' : 'Inativo'} • Horário: {message.hora}
              {message.dias > 0 && ` • ${message.dias} dia(s)`}
            </div>
          </div>
        </div>
        
        <div className="flex space-x-2">
          <Button
            variant="secondary"
            size="xs"
            onClick={onEdit}
              className="flex items-center justify-center text-gray-400 hover:text-brand-500 dark:hover:text-brand-400 transition-colors"
            title="Editar Mensagem"
          >
            <EditIcon className="w-5 h-5" />
          </Button>
          <Button
            variant="secondary"
            size="xs"
            onClick={onDelete}
            className="flex items-center justify-center text-red-500 dark:text-red-400 rounded-md transition-colors"
            title="Excluir Mensagem"
          >
            <TrashBinIcon className="w-5 h-5" />
          </Button>
        </div>
      </div>

      <div className="prose prose-sm dark:prose-invert max-w-none">
        <p className="text-gray-600 dark:text-gray-300 whitespace-pre-wrap">
          {formatMessage(message.mensagem)}
          {message.mensagem.length > 150 && (
            <span className="text-green-400 bold animate-pulse">...</span>
          )}
        </p>
      </div>
    </div>
  );
};

export default MessageCard;
