import React, { useState, useRef } from 'react';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { InfoIcon, ChevronDownIcon, ChevronUpIcon } from '@/icons';
import { toast } from 'react-hot-toast';
import { Toaster } from 'react-hot-toast';
import Select from '../ui/Select';

type FormErrors = {
  mensagem?: string;
  tipo?: string;
  dias?: string;
  hora?: string;
};

type Message = {
  id?: number;
  mensagem: string;
  tipo: 'A_VENCER' | 'NO_DIA' | 'VENCIDA' | 'COMPROVANTE' | 'MANUAL' | 'FINALIZACAO';
  status: boolean;
  hora: string;
  dias: number;
};

type MessageFormProps = {
  message?: Message;
  onSubmit: (data: Partial<Message>) => Promise<void>;
  onCancel: () => void;
};

const messageTypes = [
  { value: 'A_VENCER', label: 'A Vencer' },
  { value: 'NO_DIA', label: 'No Dia' },
  { value: 'VENCIDA', label: 'Vencida' },
  { value: 'COMPROVANTE', label: 'Comprovante' },
  { value: 'MANUAL', label: 'Manual' },
  { value: 'FINALIZACAO', label: 'Finalização' }
];

const variablesList = [
  { key: '[nomeCliente]', desc: 'Nome do cliente' },
  { key: '[valorCobranca]', desc: 'Valor da cobrança' },
  { key: '[dataVencimento]', desc: 'Data de vencimento' },
  { key: '[linkPagamento]', desc: 'Link de pagamento' },
  { key: '[nomeEmpresa]', desc: 'Nome da empresa' },
  { key: '[cnpjEmpresa]', desc: 'CNPJ da empresa' },
  { key: '[telefoneEmpresa]', desc: 'Telefone da empresa' },
  { key: '[emailEmpresa]', desc: 'Email da empresa' }
];

const MessageForm: React.FC<MessageFormProps> = ({ message, onSubmit, onCancel }) => {
  const [isVariablesExpanded, setIsVariablesExpanded] = useState(window.innerWidth >= 1024);
  const [selectedVariable, setSelectedVariable] = useState<{ key: string; x: number; y: number } | null>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const menuRef = useRef<HTMLDivElement>(null);
  const [cursorPosition, setCursorPosition] = useState<{ start: number; end: number } | null>(null);
  const [formData, setFormData] = useState<Message>({
    mensagem: message?.mensagem || '',
    tipo: message?.tipo || 'MANUAL',
    status: message?.status ?? true,
    hora: message?.hora || '09:00',
    dias: message?.dias ?? 0
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const validateForm = () => {
    const newErrors: FormErrors = {};

    if (!formData.mensagem) {
      newErrors.mensagem = 'Mensagem é obrigatória';
    } else if (formData.mensagem.length < 10) {
      newErrors.mensagem = 'Mensagem deve ter no mínimo 10 caracteres';
    }

    if (!formData.tipo) {
      newErrors.tipo = 'Tipo é obrigatório';
    }

    if (showDias && (!formData.dias && formData.dias !== 0)) {
      newErrors.dias = 'Dias é obrigatório';
    }

    if (showHora && !formData.hora) {
      newErrors.hora = 'Horário é obrigatório';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleFormSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar mensagem');
    } finally {
      setIsSubmitting(false);
    }
  };

  const showDias = formData.tipo === 'A_VENCER' || formData.tipo === 'VENCIDA';
  const showHora = !['FINALIZACAO', 'COMPROVANTE', 'MANUAL'].includes(formData.tipo);

  // Fecha o menu de contexto quando clicar fora
  React.useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setSelectedVariable(null);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const handleVariableClick = (variable: string) => {
    if (textareaRef.current && cursorPosition) {
      const text = formData.mensagem;
      const newText = text.substring(0, cursorPosition.start) + variable + text.substring(cursorPosition.end);
      const newPosition = cursorPosition.start + variable.length;
      
      setFormData({ ...formData, mensagem: newText });
      setCursorPosition({ start: newPosition, end: newPosition });
      
      // Restaurar o foco e a posição do cursor após a inserção
      setTimeout(() => {
        textareaRef.current?.focus();
        textareaRef.current?.setSelectionRange(newPosition, newPosition);
      }, 0);
    } else {
      // Se não houver posição salva, insere no final
      const newText = formData.mensagem + variable;
      setFormData({ ...formData, mensagem: newText });
      const newPosition = newText.length;
      setCursorPosition({ start: newPosition, end: newPosition });
    }
  };

  const copyVariable = async (variable: string) => {
    try {
      await navigator.clipboard.writeText(variable);
      toast.success(
        <div>
          <p className="font-medium">Variável copiada com sucesso!</p>
          <p className="text-sm text-gray-300">
            Use Ctrl+V para colar no campo de mensagem
          </p>
        </div>
      );
    } catch (error) {
      toast.error(
        <div>
          <p className="font-medium">Não foi possível copiar a variável</p>
          <p className="text-sm text-gray-300">
            Tente selecionar e copiar manualmente
          </p>
        </div>
      );
    }
  };

  return (
    <form onSubmit={handleFormSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="md:col-span-2">
          <Label htmlFor="tipo">
            Tipo de Mensagem<span className="text-error-500">*</span>
          </Label>
          {/* <select
            id="tipo"
            name="tipo"
            value={formData.tipo}
            onChange={(e) => setFormData({ ...formData, tipo: e.target.value as Message['tipo'] })}
            className="mt-1.5 w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-sm outline-none transition-all focus:border-brand-500 focus:ring-1 focus:ring-brand-500 dark:border-gray-800 dark:bg-gray-900"
          >
            {messageTypes.map(type => (
              <option key={type.value} value={type.value}>
                {type.label}
              </option>
            ))}
          </select>
          {errors.tipo && (
            <span className="text-sm text-error-500">{errors.tipo}</span>
          )} */}
                    <Select
                      id="fornecedorId"
                      name="fornecedorId"
                      // isLoading={isLoadingFornecedores}
                      value={messageTypes.filter(f => f.value === formData.tipo)}
                      options={messageTypes}
                      onChange={(option) => {
                        if (option) {
                          setFormData({
                            ...formData,
                            tipo: (option as { value: Message['tipo'] }).value
                          });
                        }
                      }}
                      placeholder="Selecione o fornecedor"
                      error={errors.tipo}
                      className="w-full"
                    />
        </div>

        {showHora && (
          <div>
            <Label htmlFor="hora">
              Horário de Envio<span className="text-error-500">*</span>
            </Label>
            <Input
              id="hora"
              type="time"
              name="hora"
              value={formData.hora || ''}
              onChange={(e) => setFormData({ ...formData, hora: e.target.value })}
              error={errors.hora}
            />
          </div>
        )}

        {showDias && (
          <div>
            <Label htmlFor="dias">
              Dias {formData.tipo === 'A_VENCER' ? 'Antes' : 'Após'}<span className="text-error-500">*</span>
            </Label>
            <Input
              id="dias"
              type="number"
              name="dias"
              min="0"
              value={formData.dias?.toString() || ''}
              onChange={(e) => setFormData({ ...formData, dias: parseInt(e.target.value) || 0 })}
              error={errors.dias}
            />
          </div>
        )}

        <div className="md:col-span-2">
          <Label htmlFor="mensagem">
            Mensagem<span className="text-error-500">*</span>
          </Label>
          <textarea
            ref={textareaRef}
            id="mensagem"
            name="mensagem"
            value={formData.mensagem}
            onChange={(e) => setFormData({ ...formData, mensagem: e.target.value })}
            onBlur={() => {
              if (textareaRef.current) {
                setCursorPosition({
                  start: textareaRef.current.selectionStart,
                  end: textareaRef.current.selectionEnd
                });
              }
            }}
            onFocus={() => {
              if (textareaRef.current && cursorPosition) {
                textareaRef.current.setSelectionRange(cursorPosition.start, cursorPosition.end);
              }
            }}
            rows={5}
          className="w-full rounded-lg border border-stroke bg-transparent px-4 py-3 outline-none focus:border-primary focus-visible:shadow-none dark:border-gray-600 dark:bg-gray-900 dark:focus:border-primary dark:text-white"
            placeholder="Digite sua mensagem aqui..."
          />
          {errors.mensagem && (
            <span className="text-sm text-error-500">{errors.mensagem}</span>
          )}
        </div>

        <div className="md:col-span-2">
          <div className="bg-gray-50 dark:bg-gray-800/50 rounded-lg p-4">
            <div 
              className="flex items-center justify-between mb-3 cursor-pointer"
              onClick={() => setIsVariablesExpanded(!isVariablesExpanded)}
            >
              <div className="flex items-center space-x-2">
                <InfoIcon className="text-gray-400" />
                <h4 className="text-sm font-medium text-gray-900 dark:text-white">
                  Variáveis Disponíveis
                </h4>
              </div>
              <button type="button" className="text-gray-400 hover:text-gray-600">
                {isVariablesExpanded ? <ChevronUpIcon /> : <ChevronDownIcon />}
              </button>
            </div>
            {isVariablesExpanded && (
              <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-3">
                {variablesList.map((variable) => (
                  <div 
                    key={variable.key}
                    onClick={(e) => {
                      setSelectedVariable({
                        key: variable.key,
                        x: e.clientX,
                        y: e.clientY
                      });
                    }}
                    className="text-xs bg-white dark:bg-gray-900 p-2 rounded border border-gray-200 dark:border-gray-700 cursor-pointer hover:border-brand-500 transition-colors"
                  >
                    <code className="font-mono text-brand-500 dark:text-brand-400">
                      {variable.key}
                    </code>
                    <p className="text-gray-600 dark:text-gray-400 mt-1">
                      {variable.desc}
                    </p>
                  </div>
                ))}
              </div>
            )}
          </div>
          {selectedVariable && (
            <div
              ref={menuRef}
              style={{
                position: 'fixed',
                left: selectedVariable.x + 'px',
                top: selectedVariable.y + 'px',
                transform: 'translate(-50%, 10px)',
                zIndex: 1000
              }}
              className="bg-white dark:bg-gray-800 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700"
            >
              <div className="py-1">
                <button
                  type="button"
                  onClick={() => {
                    handleVariableClick(selectedVariable.key);
                    setSelectedVariable(null);
                  }}
                  className="w-full px-4 py-2 text-sm text-left text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700"
                >
                  Inserir
                </button>
                <button
                  type="button"
                  onClick={() => {
                    copyVariable(selectedVariable.key);
                    setSelectedVariable(null);
                  }}
                  className="w-full px-4 py-2 text-sm text-left text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700"
                >
                  Copiar
                </button>
              </div>
            </div>
          )}
        </div>

        <div className="md:col-span-2">
          <div className="flex items-center space-x-2">
            <input
              type="checkbox"
              id="status"
              name="status"
              checked={formData.status}
              onChange={(e) => setFormData({ ...formData, status: e.target.checked })}
              className="rounded border-gray-300 text-brand-500 focus:ring-brand-500"
            />
            <Label htmlFor="status" className="!mb-0">Ativo</Label>
          </div>
        </div>
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {message ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default MessageForm;
