"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { CreateMensagemRequest, services } from '@/services/api';
import { useAuth } from '@/hooks/useAuth';
import toast from 'react-hot-toast';
import Button from '@/components/ui/Button';
import SearchInput from '@/components/form/input/SearchInput';
import MessageCard from '@/components/messages/MessageCard';
import MessageForm from '@/components/messages/MessageForm';
import { Modal } from '@/components/ui/modal';

import { Message } from '@/services/api/messages.service';

type GroupedMessages = {
  [key: string]: Message[];
};
type MessageType = 'A_VENCER' | 'NO_DIA' | 'VENCIDA' | 'COMPROVANTE' | 'MANUAL' | 'FINALIZACAO' | 'ALL';


export default function MensagensPage() {
  const { user: currentUser } = useAuth();
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedMessage, setSelectedMessage] = useState<Message | null>(null);
  const [activeTab, setActiveTab] = useState<MessageType>('ALL');

  const loadMessages = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.mensagem.list();
      setMessages(response.data);
    } catch (error) {
      toast.error('Erro ao carregar mensagens');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadMessages();
  }, [loadMessages]);

  const handleEdit = (message: Message) => {
    setSelectedMessage(message);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta mensagem?')) return;

    try {
      await services.mensagem.delete(id);
      toast.success('Mensagem excluída com sucesso');
      loadMessages();
    } catch (error) {
      toast.error('Erro ao excluir mensagem');
    }
  };

  const handleSave = async (data: Partial<Message>) => {
    try {
      if (selectedMessage) {
        await services.mensagem.update(selectedMessage.id, data);
        toast.success('Mensagem atualizada com sucesso');
      } else {
        await services.mensagem.create(data as unknown as CreateMensagemRequest);
        toast.success('Mensagem criada com sucesso');
      }
        setIsModalOpen(false);
        setSelectedMessage(null);
        loadMessages();
    } catch (error) {
      toast.error('Erro ao salvar mensagem');
    }
  };

  const filteredMessages = messages.filter(message => 
    message.mensagem.toLowerCase().includes(searchTerm.toLowerCase()) &&
    (activeTab === "ALL" || activeTab === message.tipo)
  );

  const tabs = [
    { value: 'A_VENCER', label: 'A Vencer' },
    { value: 'NO_DIA', label: 'No Dia' },
    { value: 'VENCIDA', label: 'Vencida' },
    { value: 'COMPROVANTE', label: 'Comprovante' },
    { value: 'MANUAL', label: 'Manual' },
    { value: 'FINALIZACAO', label: 'Finalização' },
    { value: 'ALL', label: 'Todas' },
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Mensagens
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie as mensagens automáticas do sistema
            </p>
          </div>
          <Button 
            variant="primary"
            onClick={() => {
              setSelectedMessage(null);
              setIsModalOpen(true);
            }}
          >
            Nova Mensagem
          </Button>
        </div>

        <div className="mt-6">
          <div className="flex flex-wrap gap-2 mb-6">
            {tabs.map((tab) => (
              <button
                key={tab.value}
                onClick={() => setActiveTab(tab.value as MessageType)}
                className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors
                  ${activeTab === tab.value
                    ? 'bg-brand-500 text-white dark:bg-brand-400 dark:text-gray-900'
                    : 'bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-gray-700'
                  }`}
              >
                {tab.label}
              </button>
            ))}
          </div>

          <div className="w-full md:w-96 mb-6">
            <SearchInput
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Buscar mensagens..."
            />
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {filteredMessages.length > 0 ? (
            filteredMessages.map((message) => (
              <MessageCard
                key={message.id}
                message={message}
                onEdit={() => handleEdit(message)}
                onDelete={() => handleDelete(message.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma mensagem encontrada para este tipo.
              </p>
            </div>
          )}
        </div>
      )}

      <Modal isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedMessage(null);
        }} className="max-w-[800px] m-4">
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedMessage ? 'Editar Mensagem' : 'Nova Mensagem'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedMessage ? 'Atualize as informações do mensagem.' : 'Preencha os dados para criar uma nova mensagem.'}
            </p>
          </div>
        <MessageForm
          message={selectedMessage || undefined}
          onSubmit={handleSave}
          onCancel={() => {
            setIsModalOpen(false);
            setSelectedMessage(null);
          }}
        />
        </div>
      </Modal>
      {/* <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedMessage(null);
        }}
        title={selectedMessage ? "Editar Mensagem" : "Nova Mensagem"}
      >
        <MessageForm
          message={selectedMessage || undefined}
          onSubmit={handleSave}
          onCancel={() => {
            setIsModalOpen(false);
            setSelectedMessage(null);
          }}
        />
      </Modal> */}
    </div>
  );
}
